import React from 'react'

export default class Map extends React.Component {
	render(){
		return(
			<div id="map-container">
				<div class="map-title blue">
					<h1>Map to Blue Hacks 2019</h1>
					<h2>
						<span class="white">Rizal Library </span> <br />
						<span class="white">Ateneo de Manila University </span>
					</h2>
				</div>
				<iframe title="Blue Hacks Map" src="https://snazzymaps.com/embed/28331" class="map"></iframe>
			</div>
		);
	}
}


